.App {
  text-align: center;
}

textarea {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fff0;
}

:root {
  --primary-color: #4287f5;
  --grey-background: #ededed;
  --grey-dark-1: #d6d6d6;
}



::-webkit-scrollbar {
  width: 10px;
  height: 15px;
  border-radius: 5px;
}


::-webkit-scrollbar-track {
  background: #f1f1f1;

}


::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 5px;
  width: 6px;
  height: 13px;
}


::-webkit-scrollbar-thumb:hover {
  background: #ddd;
}


.hide {
  display: none !important;
}



.board {
  display: inline-flex;
}



[data-rbd-droppable-id="all-columns"] {
  display: inline-flex;
  height: 100vh;
 
}

.board-container {
  display: inline-flex;
}

.list-container {
  display: flex;
 
  height: fit-content;

  
 
 
  flex-direction: column;

  width: 270px;

  margin: 0px 5px 0px 5px;
}

.list-composer {
 
  display: grid;
  grid-template-columns: 1fr;
 
  min-height: 22px;
  border-radius: 8px;
 
  text-align: center;

 
 
  background-color: var(--grey-background);
 

  padding: 9px 15px;
 
}

.list-addlist {
  box-sizing: border-box;
  user-select: none;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  line-height: 30px;
  height: 100%;
 

  border-radius: 3px; 
  transition: 0.2s;
}

.list-addlist:hover {
  background-color: var(--grey-dark-1);
}

.list-area {
  cursor: grab;
  display: flex;
  flex-direction: column;

  width: 270px;

  margin: 0px 5px 0px 5px;
}


.list-heading {
 
 
 
 
 /* display: flex; */

  display: grid;
  grid-template-columns: 7fr 1fr;

 /* justify-content: space-between; */
 
 

  padding: 8px 15px;
  width: 240px;

 
  overflow: hidden;
 

  min-height: 22px;
 
  background-color: var(--grey-background);
  border-radius: 8px 8px 0px 0px;
}




.overlay {
  display: inherit;
  box-sizing: border-box;
  opacity: 0;
  cursor: pointer;
  position: absolute;

  z-index: 2;
 
  height: 100%;
 
}


.list-overlay {
  cursor: grab;
  position: absolute;

  z-index: 2;
  width: 240px;
  height: 50%;

 
  font-size: 16px;
  font-weight: 400;
}

.list-input {
  
  white-space: pre-wrap;

  cursor: pointer;
 

  min-height: 22px;
  
 
 
 
  /* display: inline-block; */
  overflow-wrap: break-word;
  overflow: hidden;

 width: 100%;

  margin: 0;

 
 

  z-index: 1;
  padding: 2px;
 

  font-size: 16px;
 
  font-weight: 400;

  background-color: #fff0;

  border: none;
  outline: none;
  resize: none;
 
 
  border-radius: 3px;

  outline-color: var(--primary-color);
}

.list-input:focus {
  cursor: text;
}

.list-input:hover {
  background-color: #f5f5f5;
}

.list-input:focus {
  outline: auto;

  background-color: #f5f5f5;
  outline-color: var(--primary-color);

}

.list-manager {
  /* margin: 0px 5px; */
  margin-left: 10px;
  /* box-sizing: border-box; */
  width: 26px;
  height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  /* font-size: 16px; */
  /* line-height: 26px; */
  transition: 0.2s;
}

.list-manager:hover {
  background-color: var(--grey-dark-1);
}

.list-manager:focus {
  background-color: var(--grey-dark-1);
}

.item-list {

  text-align: center;

  margin: 0;
  background-color: var(--grey-background);
  width: 240px;

  padding: 4px 15px 0px;

  display: grid;
  grid-template-columns: 1fr;

}

.list-bottom {
  border-radius: 0px 0px 8px 8px;
}

.list-footer {


 

  padding: 0px 15px 10px;
  width: 240px;


  min-height: 42.5px;
  background-color: var(--grey-background);
  border-radius: 0px 0px 8px 8px;


}

.footer-addcard {

  user-select: none;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  line-height: 30px;



  margin-top: 6px;

 
  height: 30px;

  border-radius: 3px; 
  transition: 0.2s;

}

.footer-addcard:hover {

  background-color: var(--grey-dark-1);
}

.footer-inputcard {


  margin-bottom: 10px;


  width: 240px;
}

.composer-container {
 
 

  margin-bottom: 5px;

  display: block;
  min-height: 45px;
 
  text-align: left;

}

.composer-input {

  white-space: pre-wrap;

  overflow-wrap: break-word;
 
 
  display: block;

 
  border-radius: 5px;
  background-color: #fafafa;
  box-shadow: 0 1px 0#BBBBBB;
  color: #172b4d;

 
  border-radius: 5px;
 
 
  
  padding: 5px 10px 7px;
  text-align: left;

  color: #172b4d;

  font-size: 1em;
  outline: none;
  box-sizing: border-box;
  width: 100%;
  border: none;
 
  resize: vertical;
 
 
 

  overflow: hidden;

  min-height: 42px;
 
}

.composer-control {
  margin-top: 8px;
  height: 30px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}

.composer-addcard {
 
  height: 100%;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  border-radius: 3px;
  vertical-align: middle;
  font-size: 0.9em;
  line-height: 0.9em;
  transition: 0.2s;
}

.composer-addcard:hover {
 
  background-color: var(--primary-color);
  color: white;
}

.composer-addcard:active {
  transition: none;
 
}

.composer-addcard-icon {
  cursor: pointer;
  margin: 4px;
  color: #888;
  transition: 0.4s;
  width: 30px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.composer-addcard-icon:hover {
  transform: rotate(90deg);
  color: #aaa;
}

.addcardicon {

  margin-right: 4px;
}

.task {

  
  font-size: 1em;


  display: grid;
  grid-template-columns: 1fr;
 
 
  height: fit-content;

  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #fafafa;
  box-shadow: 0 1px 0#BBBBBB;
  color: #172b4d;



  overflow-wrap: break-word;

 
  overflow-y: auto;
  overflow-x: hidden;
  min-height: 45px;
  padding: 5px 10px 7px;
  text-align: left;
  position: relative;
}

.task-input {
 
 
 
  white-space: pre-wrap;
  display: block;
  opacity: 1;
 
  padding: 0;
  font-size: 1em;
  overflow-wrap: break-word;
  overflow: hidden;
  box-sizing: border-box;
  width: 100%;
  outline: none;
  border: none;
  resize: none;
 
  cursor: pointer;
}

.task-input:focus {
  cursor: text;
}



.fullw {
  width: 100%;
}

.fullh {
  height: 100%;
}

